
'use strict';
import AnimatedImplementation from './AnimatedImplementation'
import Text from './../../components/Text/Text.web'
import Image from './../../components/Image/Image.web'
import View from './../../components/View/View.web'
import ScrollView from './../../components/ScrollView/index.web'



let AnimatedScrollView;
let AnimatedObj={
    ...AnimatedImplementation,
    View: AnimatedImplementation.createAnimatedComponent(View),
    Text: AnimatedImplementation.createAnimatedComponent(Text),
    Image: AnimatedImplementation.createAnimatedComponent(Image),
	get ScrollView() {
		// Make this lazy to avoid circular reference.
		if (!AnimatedScrollView) {
			AnimatedScrollView = AnimatedImplementation.createAnimatedComponent(ScrollView);
		}
		return AnimatedScrollView;
	},
};

export default AnimatedObj;




